<template>
  <div class="cms-list-layout">
    <cms-prompt collapse='true'>
      <li>显示店铺内的所有账户信息</li>
      <li>可对非登录账户数据进行修改操作</li>
    </cms-prompt>
    <!-- 筛选区 -->
    <section class="cms-list-query">
      <div class="query-group">
        <label>账户</label>
        <el-input v-model="queryParams.username" @keyup.native.enter="query"></el-input>
      </div>
      <div class="query-group">
        <cms-button size="small" :colorPicker='true' @click="query">查询</cms-button>
      </div>
    </section>
    <!-- 操作区 -->
    <section class="cms-batch">
      <cms-button    icon="add" type="success" size="small" @click="$routerLink('/admin/save','save')">新增</cms-button>
      <cms-button   icon="error" type="error" size="small" @click="deleteDatas()">批量删除</cms-button>
    </section>
    <el-table :data="tableData" style="width: 100%" stripe v-loading="loading" @selection-change='getCheckItems' @row-click='checkRows' ref='dataTable'>
      <el-table-column type="selection" width="46" fixed='left'></el-table-column>
      <el-table-column prop="id" label="id" width="60"></el-table-column>
      <el-table-column prop="username" label="账户" min-width='120'></el-table-column>
      <el-table-column prop="roleString" label="账户组" min-width="250"></el-table-column>
      <el-table-column prop="enabled" label="是否启用" width="110">
        <template slot-scope="scope">
          <cms-switch v-model="scope.row.enabled" :id='scope.row.id' @change="setDisable"></cms-switch>
        </template>
      </el-table-column>
      <el-table-column prop="" label="最后登录ip" min-width="170">
        <template slot-scope="scope">
          <span>{{scope.row.lastLoginIp?scope.row.lastLoginIp:'暂无'}}</span>
        </template>
      </el-table-column>
      <el-table-column prop="lastLoginTime" label="最后登陆时间" min-width="170">
        <template slot-scope="scope">
          {{scope.row.lastLoginTime?$moment(scope.row.lastLoginTime):'暂无'}}
        </template>
      </el-table-column>
      <el-table-column prop="createTime" label="创建时间" min-width="170">
        <template slot-scope="scope">
          {{$moment(scope.row.createTime)}}
        </template>
      </el-table-column>
      <el-table-column label="操作" width='175' fixed="right" align="center">
        <div slot-scope="scope">
          <cms-button    icon='edit' size='small' type='info' @click.stop="$routerLink('/admin/update','update',{id:scope.row.id})">编辑</cms-button>
          <cms-button  icon='error' size='small' type='info' @click.stop="deleteData(scope.row.id)">删除</cms-button>
        </div>
      </el-table-column>
    </el-table>
    <cms-pagination @change="getPage" :total='total'></cms-pagination>
  </div>
</template>
<script>
import list from '@/mixins/list'
import {
  adminList,
  adminDelete,
  adminDisable,
  adminEnable
} from './../../../../api/admin/admin.js'
var defaultData = {
  enabled: '',
  username: '',
  startTime: '',
  endTime: '',
  size: 15,
  page: 1
}
export default {
  name: 'adminList',
  mixins: [list],
  data() {
    return {
      queryParams: Object.assign({}, defaultData)
    }
  },
  methods: {
    deleteData(id) {
      // 包装一层删除的操作
      this.deleteTableData(adminDelete, id) // 这个才是真实的删除方法
    },
    deleteDatas() {
      this.deleteTableDatas(adminDelete, this.ids) // 这个才是真实的删除方法
    },
    setTimer(timer) {
      // 设置时间区间
      this.queryParams.startTime = timer[0]
      this.queryParams.endTime = timer[1]
      this.query()
    },
    setDisable(status, id) {
      if (status) {
        adminEnable({ id: id }).then(res => {
          // 启用
          if (res.code === 200) {
            this.$successMessage('启用成功')
          } else {
            this.$errorMessage('启用失败')
          }
        })
      } else {
        adminDisable({ id: id }).then(res => {
          // 禁用
          if (res.code === 200) {
            this.$successMessage('禁用成功')
          } else {
            this.$errorMessage('禁用失败')
          }
        })
      }
    }
  },
  created() {
    // 首次触发
    this.getTableData(adminList, this.queryParams)
  }
}
</script>

<style>
</style>



